<template>
    <div class="container">
        <div class="result">
            <p id="result-box">{{ currentNumber }}</p>
        </div>

        <div class="buttons">
            <div class="button action-btn" id="clear" @click="clear">C</div>
            <!-- <div class="button action-btn">MC</div> -->
            <div class="button action-btn" @click="plusminus">+/-</div>
            <div class="button num-button  " @click="handleOperation('%')">%</div>
            <div class="button num-button  " @click="handleOperation('/')">/</div>
            <div class="button num-button " @click="addNumber('7')">7</div>
            <div class="button num-button " @click="addNumber('8')">8</div>
            <div class="button num-button  " @click="addNumber('9')">9</div>
            <div class="button num-button " @click="handleOperation('*')">*</div>
            <div class="button num-button  " @click="addNumber('4')">4</div>
            <div class="button num-button  " @click="addNumber('5')">5</div>
            <div class="button num-button  " @click="addNumber('6')">6</div>
            <div class="button num-button " @click="handleOperation('-')"> -</div>
            <div class="button num-button  " @click="addNumber('1')">1</div>
            <div class="button num-button  " @click="addNumber('2')">2</div>
            <div class="button num-button  " @click="addNumber('3')">3</div>
            <div class="button num-button " @click="handleOperation('+')">+</div>
            <div class="button num-button  " @click="addNumber('0')">0</div>
            <div class="button num-button  " @click="addDot">.</div>
            <div class="button" @click="equal"> =</div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            currentNumber: '',
            prevNumber: '',
            operation: '',
        };
    },
    methods: {
        addNumber(number) {
            if (this.currentNumber == 0 && number == 0) return;
            this.currentNumber += number;
        },
        addDot() {
            if (this.currentNumber.includes('.')) return;
            this.currentNumber += '.';
        },
        clear() {
            this.currentNumber = '';
        },
        plusminus() {
            if (parseFloat(this.currentNumber) > 0) {
                this.currentNumber = '-' + this.currentNumber;
            } else {
                this.currentNumber = this.currentNumber.substring(1);
            }
        },
        handleOperation(op) {
            this.prevNumber = this.currentNumber;
            this.currentNumber = '';
            this.operation = op;
        },
        equal() {
            
            switch (this.operation) {
                case '+':
                    this.currentNumber = parseFloat(this.prevNumber) + parseFloat(this.currentNumber) 
                    break;
                case '*':
                    this.currentNumber = parseFloat(this.prevNumber) * parseFloat(this.currentNumber)  
                    break
                case '/':
                    this.currentNumber = parseFloat(this.prevNumber) / parseFloat(this.currentNumber) 
                    break
                case '%':
                    this.currentNumber = parseFloat(this.prevNumber) % parseFloat(this.currentNumber)  
                    break
            }
            this.currentNumber = this.currentNumber.toString();
        },
    },
};
</script>
